import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonButtons,
  IonMenuButton,
  IonContent,
  IonBackButton,
  IonButton,
  IonIcon,
  useIonRouter,
  IonProgressBar,
} from "@ionic/react";
import { useRef, useState } from "react";
import { useParams } from "react-router";

import "./CropImage.css";
import { saveOutline } from "ionicons/icons";
import useStore from "../util/store";

import Cropper from "react-cropper";
import "cropperjs/dist/cropper.css";

const CropImage: React.FC = () => {
  const { activeTweet, setState } = useStore((st) => st);
  const { link: ImageLink } = useParams<{ link: string }>();
  const { tweetIndex, imageIndex }: { tweetIndex: number; imageIndex: number } =
    JSON.parse(atob(ImageLink || ""));
  const cropperRef = useRef<HTMLImageElement>(null);

  const router = useIonRouter();

  return (
    <>
      <IonHeader>
        <IonToolbar>
          <div className="header-neon neon">
            <IonBackButton defaultHref="home" />
            <span style={{ marginLeft: 16 }}>Crop Image</span>
            <IonButtons style={{ marginLeft: "auto" }}>
              <IonButton
                onClick={() => {
                  if (cropperRef?.current && activeTweet) {
                    const imageElement: any = cropperRef?.current;
                    const cropper: any = imageElement?.cropper;

                    activeTweet[tweetIndex].media[imageIndex].url = cropper
                      .getCroppedCanvas()
                      .toDataURL();
                    setState({ activeTweet });
                  }
                  router.goBack();
                }}
              >
                <IonIcon icon={saveOutline} />
              </IonButton>
              <IonMenuButton />
            </IonButtons>
          </div>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <div
          style={{
            position: "relative",
            margin: "auto",
            display: "flex",
            maxWidth: "90%",
            height: "100%",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          {activeTweet && (
            <Cropper
              src={activeTweet[tweetIndex].media[imageIndex].url}
              style={{ width: "100%" }}
              // Cropper.js options
              initialAspectRatio={16 / 9}
              guides={false}
              ref={cropperRef}
              checkCrossOrigin={false}
              crossOrigin="anonymous"
              modal
            />
          )}
        </div>
      </IonContent>
    </>
  );
};

export default CropImage;
