import React, {
  Dispatch,
  SetStateAction,
  useEffect,
  useRef,
  useState,
} from "react";
import {
  IonButton,
  IonContent,
  IonHeader,
  IonIcon,
  IonText,
  IonToolbar,
  IonProgressBar,
  useIonToast,
  IonMenuButton,
  IonButtons,
  IonFooter,
  IonLabel,
  useIonRouter,
  useIonActionSheet,
  useIonPopover,
  IonItem,
  IonList,
  IonModal,
} from "@ionic/react";
import { nanoid } from "nanoid";

import "./Home.css";
import "../util/reactResize.css";

//@ts-ignore
import Twemoji from "twemoji";

import {
  accessibilityOutline,
  crop,
  easelOutline,
  ellipsisVertical,
  eyeOffOutline,
  eyeOutline,
  gitNetworkOutline,
  linkOutline,
  magnetOutline,
  repeatOutline,
  saveOutline,
} from "ionicons/icons";

import useStore from "../util/store";
import { ResizableBox } from "react-resizable";
import ContentEditable from "react-contenteditable";
//@ts-ignore
import domtoimage from "retina-dom-to-image";
import Draggable from "react-draggable";
//@ts-ignore
import ImageFadeIn from "react-image-fade-in";
import profiles from "../util/profiles";
import memes from "../util/memes";
import { useHistory, useLocation } from "react-router";

const Header: React.FC<{
  getImage: () => Promise<void>;
  Blurring: () => void;
  setAspectRatio: Dispatch<SetStateAction<boolean>>;
  aspectRatio: boolean;
  barsOff: boolean;
}> = ({ getImage, Blurring, barsOff, setAspectRatio, aspectRatio }) => (
  <IonHeader hidden={barsOff}>
    <IonToolbar>
      <div className="header-neon neon">
        <span>mocholads</span>
        <IonButtons style={{ marginLeft: "auto" }}>
          <IonButton fill="clear" onClick={Blurring}>
            <IonIcon icon={eyeOffOutline} />
          </IonButton>
          <IonButton
            fill={aspectRatio ? "solid" : "clear"}
            onClick={() => setAspectRatio(!aspectRatio)}
          >
            <IonIcon icon={magnetOutline} />
          </IonButton>
          <IonButton fill="clear" onClick={getImage}>
            <IonIcon icon={saveOutline} />
          </IonButton>
          <IonMenuButton />
        </IonButtons>
      </div>
    </IonToolbar>
  </IonHeader>
);

interface Props {
  router: HTMLIonRouterOutletElement | null;
}

const Home: React.FC<Props> = ({ router }) => {
  const { setState, activeTweet } = useStore((st) => st);
  const [blurred, setBlurred] = useState(false);
  const [lockAspectRatio, setLockAspectRatio] = useState(true);
  const windowWidth = useRef(window.innerWidth).current;
  const activeTweetIndex = useRef<number | null>(null);
  const activeImageIndex = useRef<number | null>(null);
  const [modalOpened, setModalOpened] = useState(false);
  const Router = useIonRouter();
  const [isLoading, setIsLoading] = useState(false);

  const [hasImageBlur, setHasImageBlur] = useState(true);
  const [groupedTweets, setGroupedTweets] = useState(false);
  const [darkTheme, setDarkTheme] = useState(true);
  const [hasConnector, setHasConnector] = useState(false);
  const [aspectRatio, setAspectRatio] = useState<
    "square" | "portraite" | "story"
  >("portraite");
  const [ShowToast, DismissToast] = useIonToast();

  const [bgIndex, setBgIndex] = useState(0);
  const TweetContainer = useRef<HTMLDivElement | null>(null);
  const innerTweetContainer = useRef<HTMLDivElement | null>(null);

  const [
    presentAction,
    // dismissAction
  ] = useIonActionSheet();
  const [presentImagePopover, dismissImagePopover] = useIonPopover(
    <IonList>
      <IonItem
        button
        onClick={() => {
          if (
            activeTweetIndex.current == null ||
            activeImageIndex.current == null
          )
            return;
          Router.push(
            "/crop-image/" +
              btoa(
                JSON.stringify({
                  tweetIndex: activeTweetIndex.current,
                  imageIndex: activeImageIndex.current,
                })
              )
          );
          dismissImagePopover();
        }}
      >
        <IonLabel>Crop</IonLabel>
        <IonIcon icon={crop} />
      </IonItem>
      <IonItem
        button
        onClick={() => {
          if (
            activeTweetIndex.current == null ||
            activeImageIndex.current == null
          )
            return;
          activeTweet[activeTweetIndex.current].media[
            activeImageIndex.current
          ].hidded = true;
          setState({ activeTweet });
          dismissImagePopover();
        }}
      >
        <IonLabel>Hide</IonLabel>
        <IonIcon icon={eyeOffOutline} />
      </IonItem>
      <IonItem
        button
        onClick={async () => {
          //create file input
          const input = document.createElement("input");
          input.type = "file";
          input.accept = "image/png,image/jpeg,image/jpg";

          input.onchange = (event) => {
            if (!input.files) return;
            const reader = new FileReader();
            reader.onload = () => {
              if (
                activeTweetIndex.current == null ||
                activeImageIndex.current == null
              )
                return;
              activeTweet[activeTweetIndex.current].media[
                activeImageIndex.current
              ].url = reader.result as string;
              setState({ activeTweet });
            };
            reader.readAsDataURL(input.files[0]);
          };
          input.click();
          dismissImagePopover();
        }}
      >
        <IonLabel>Replace</IonLabel>
        <IonIcon icon={repeatOutline} />
      </IonItem>
    </IonList>
  );

  const Blurring = () => {
    setBlurred(!blurred);
  };
  const getImage = async () => {
    if (isLoading) return;
    const nodeParent = TweetContainer.current;
    //const nodeChild = innerTweetContainer.current;
    if (nodeParent == null) return;
    setBlurred(true);
    setIsLoading(true);
    try {
      const image = await domtoimage.toPng(nodeParent);
      const link = document.createElement("a");
      link.href = image;
      link.download = nanoid() + ".png";
      link.click();
      ShowToast({
        buttons: [{ text: "OK", handler: () => DismissToast() }],
        duration: 3000,
        color: "success",
        message: "Tweet captured successfully!",
      });
    } catch (e) {
      ShowToast({
        buttons: [{ text: "OK", handler: () => DismissToast() }],
        duration: 3000,
        color: "danger",
        message: "There was an error!",
      });
    }
    setIsLoading(false);
    setBlurred(false);
  };
  const location = useLocation();
  const nav = useHistory();

  useEffect(() => {
    if (!location.search.includes("modalOpened=true")) {
      setModalOpened(false);
    }
  }, [location]);

  return (
    <>
      <Header
        aspectRatio={lockAspectRatio}
        setAspectRatio={setLockAspectRatio}
        getImage={getImage}
        Blurring={Blurring}
        barsOff={blurred}
      />
      <IonContent
        fullscreen
        style={{ height: blurred ? "100%" : "calc(100% - 130px)" }}
      >
        {/* <Twemoji noWrapper={true}> */}
        <div
          onClick={() => {
            if (blurred) setBlurred(false);
          }}
          style={{
            position: isLoading ? "relative" : "initial",
            //backgroundColor: "#15202b",
          }}
        >
          {isLoading && (
            <IonProgressBar
              style={{ position: "absolute", zIndex: "99", top: 0 }}
              type="indeterminate"
            ></IonProgressBar>
          )}
          {activeTweet && activeTweet.length > 0 && (
            <div
              ref={TweetContainer}
              style={{
                width: windowWidth,
                height:
                  aspectRatio == "portraite"
                    ? windowWidth * (5 / 4)
                    : aspectRatio == "story"
                    ? windowWidth * (16 / 9)
                    : windowWidth,
                backgroundColor: darkTheme ? "#15202b" : "black",
                alignItems: "center",
                display: "flex",
                position: "relative",
                flexDirection: "column",
                overflow: "hidden",
              }}
              className="tweet-container"
            >
              <div
                className="bounder"
                style={{
                  position: hasConnector ? "relative" : "initial",
                  flexDirection: "column",
                  display: "flex",
                  width: "100%",
                  margin: "auto",
                  overflow: "hidden",
                  padding: 8,
                  height:
                    (aspectRatio == "portraite"
                      ? windowWidth * (5 / 4)
                      : aspectRatio == "story"
                      ? windowWidth * (16 / 9)
                      : windowWidth) - 16,
                  //height: "100%",
                }}
              >
                {hasConnector && activeTweet.length > 1 && (
                  <div
                    style={{
                      position: "absolute",
                      width: 2,
                      backgroundColor: "rgb(61, 84, 102)",
                      height: "calc(100% - 48px)",
                      left: 44,
                      top: 24,
                      zIndex: 0,
                    }}
                  />
                )}
                {activeTweet.length > 0 &&
                  activeTweet.map((Tweet, index) => (
                    <Draggable
                      key={Tweet.userName + "_" + index}
                      axis="y"
                      handle={".tweet-name-username_" + index}
                      bounds=".bounder"
                      allowAnyClick
                    >
                      <div
                        className="inner-container"
                        style={{
                          width: "100%",
                          padding: 8,
                          backgroundColor: hasConnector
                            ? bgIndex == index
                              ? darkTheme
                                ? "#15202b"
                                : "black"
                              : "transparent"
                            : "transparent",
                          zIndex: hasConnector
                            ? bgIndex == index
                              ? 99
                              : "initial"
                            : "initial",
                        }}
                        ref={innerTweetContainer}
                      >
                        <div
                          className={"tweet-header_" + index}
                          style={{
                            display: "flex",
                            alignItems: "center",
                            width: "100%",
                            boxSizing: "border-box",
                          }}
                        >
                          <div
                            style={{
                              boxShadow: "rgb(0 0 0 / 2%) 0px 0px 2px inset",
                              display: "flex",
                              alignItems: "center",
                              justifyContent: "center",
                              flexShrink: 0,
                              width:
                                activeTweet.length > 1 && hasConnector
                                  ? 58
                                  : 48,
                              height:
                                activeTweet.length > 1 && hasConnector
                                  ? 58
                                  : 48,
                              background:
                                activeTweet.length > 1 && hasConnector
                                  ? darkTheme
                                    ? "#15202b"
                                    : "black"
                                  : "none",
                            }}
                          >
                            <ImageFadeIn
                              className={`${
                                blurred &&
                                hasImageBlur &&
                                Tweet.userName.trim() != "mocholmag" &&
                                " blurred"
                              } tweet-name-username_${index}`}
                              src={Tweet.profileImage}
                              style={{
                                flexShrink: 0,
                                width: 48,
                                height: 48,
                                borderRadius: "100%",
                                zIndex: 90,
                              }}
                            />
                          </div>

                          <div
                            style={{
                              display: "flex",
                              flexDirection: "column",
                              maxWidth: "80%",
                              //width: "100%",
                            }}
                          >
                            <div
                              style={{
                                marginLeft:
                                  activeTweet.length > 1
                                    ? hasConnector
                                      ? 10
                                      : 14
                                    : 16,
                                display: "flex",
                                flexDirection:
                                  activeTweet.length > 1 ? "row" : "column",
                                alignItems: "baseline",
                                justifyContent:
                                  activeTweet.length > 1 ? "initial" : "center",
                              }}
                            >
                              <div
                                //className={`${blurred && " blurred"}`}
                                style={{
                                  fontWeight: 700,
                                  color: "white",
                                  direction: "rtl",
                                  textAlign: "left",
                                  maxWidth:
                                    activeTweet.length > 1 ? "initial" : "120%",
                                  whiteSpace: "nowrap",
                                  textOverflow: "ellipsis",
                                  overflow: "hidden",
                                }}
                              >
                                <ContentEditable
                                  html={Tweet.name}
                                  style={{ outline: "none", minWidth: 20 }}
                                  onChange={(ev) => {
                                    Tweet.name = ev.target.value;
                                    setState({ activeTweet });
                                  }}
                                />
                              </div>
                              <div
                                className={`${
                                  blurred &&
                                  Tweet.userName.trim() != "mocholmag" &&
                                  "blurred"
                                }`}
                                style={{
                                  color: "rgb(136, 153, 166)",
                                  fontSize: 14,
                                  minWidth: "80px",
                                  paddingLeft: activeTweet.length > 1 ? 5 : 0,
                                }}
                              >
                                {Tweet.userName}
                              </div>
                            </div>
                            {Tweet.mentions.length > 0 &&
                              activeTweet.length > 1 && (
                                <div
                                  className="subHeader"
                                  style={{
                                    fontSize: 14,
                                    width: "90%",
                                    marginLeft: hasConnector ? 10 : 14,
                                    textOverflow: "clip",
                                    whiteSpace: "nowrap",
                                    overflow: "hidden",
                                    marginTop: 3,
                                  }}
                                >
                                  <span
                                    style={{
                                      color: "rgb(136, 153, 166)",
                                    }}
                                  >
                                    Replying to
                                  </span>
                                  {Tweet.mentions.map((mention, index) => (
                                    <IonText key={mention.userId}>
                                      {index > 0 ? (
                                        <>
                                          <IonText
                                            style={{
                                              color: "rgb(136, 153, 166)",
                                            }}
                                          >
                                            {" "}
                                            and{" "}
                                          </IonText>
                                          <IonText
                                            className={`${
                                              blurred &&
                                              mention.userName.trim() !=
                                                "mocholmag" &&
                                              " blurred"
                                            }`}
                                            color="primary"
                                          >
                                            @{mention.userName}
                                          </IonText>
                                        </>
                                      ) : (
                                        <IonText
                                          className={`${
                                            blurred &&
                                            mention.userName.trim() !=
                                              "mocholmag" &&
                                            " blurred"
                                          }`}
                                          color="primary"
                                        >
                                          {" @" + mention.userName}
                                        </IonText>
                                      )}
                                    </IonText>
                                  ))}
                                </div>
                              )}
                          </div>

                          <IonButton
                            onClick={() => {
                              presentAction({
                                buttons: [
                                  {
                                    text: "Show hided media",
                                    icon: eyeOutline,
                                    handler: () => {
                                      Tweet.media.forEach(
                                        (media) => (media.hidded = false)
                                      );
                                      setState({ activeTweet });
                                    },
                                  },
                                  ...(activeTweet.length > 1
                                    ? [
                                        {
                                          text: "Set active background",
                                          icon: easelOutline,
                                          handler: () => setBgIndex(index),
                                        },
                                      ]
                                    : []),
                                  {
                                    text: "change Profile photo",
                                    icon: accessibilityOutline,
                                    handler: () => {
                                      activeTweetIndex.current = index;
                                      nav.push(
                                        nav.location.pathname +
                                          "?modalOpened=true"
                                      );
                                      setModalOpened(true);
                                    },
                                  },
                                  {
                                    text: "toggle Replay text",
                                    icon: repeatOutline,
                                    handler: () => {
                                      Tweet.hasReplay = !Tweet.hasReplay;
                                      setState({ activeTweet });
                                    },
                                  },
                                ],
                              });
                            }}
                            expand="full"
                            style={{
                              marginLeft: "auto",
                              "--color": "white",
                              "--color-focused":
                                "const(--ion-color-primary, #3880ff)",
                              "--color-hover":
                                "const(--ion-color-primary, #3880ff)",
                              "--padding-start": 0,
                              "--padding-end": 0,

                              width: "unset",
                            }}
                            fill="clear"
                            shape="round"
                          >
                            <img
                              src={ellipsisVertical.replace(
                                "class='ionicon'",
                                `class='ionicon' style='fill: ${"rgb(136 153 166)"}'`
                              )}
                              color="white"
                              style={{
                                width: activeTweet.length > 1 ? 16 : 20,
                                marginBottom:
                                  activeTweet.length > 1 ? "auto" : "initial",
                              }}
                            />
                          </IonButton>
                        </div>

                        {Tweet.hasReplay && activeTweet.length == 1 && (
                          <div
                            className="subHeader"
                            style={{
                              fontSize: 14,
                              width: "100%",
                              marginTop: 8,
                            }}
                          >
                            <span
                              style={{
                                color: "rgb(136, 153, 166)",
                              }}
                            >
                              Replying to
                            </span>
                            {Tweet.mentions.map((mention, index) => (
                              <IonText key={mention.userId}>
                                {index > 0 ? (
                                  <>
                                    <IonText
                                      style={{
                                        color: "rgb(136, 153, 166)",
                                      }}
                                    >
                                      {" "}
                                      and{" "}
                                    </IonText>
                                    <IonText
                                      className={`${
                                        blurred &&
                                        mention.userName.trim() !=
                                          "mocholmag" &&
                                        " blurred"
                                      }`}
                                      color="primary"
                                    >
                                      @{mention.userName}
                                    </IonText>
                                  </>
                                ) : (
                                  <IonText
                                    className={`${
                                      blurred &&
                                      mention.userName.trim() != "mocholmag" &&
                                      " blurred"
                                    }`}
                                    color="primary"
                                  >
                                    {" @" + mention.userName}
                                  </IonText>
                                )}
                              </IonText>
                            ))}
                          </div>
                        )}

                        <div
                          className="tweet-body"
                          style={{
                            width: "100%",
                            // paddingTop: activeTweet.length > 1 ? 0 : 0,
                            paddingLeft:
                              activeTweet.length > 1 && hasConnector ? 66 : 0,
                          }}
                        >
                          <ContentEditable
                            style={{
                              outline: "none",
                              color: "white",
                              direction: "rtl",
                              //whiteSpace: "pre",
                              marginTop: 8,
                              padding: "0 8px",
                              fontSize: activeTweet.length > 1 ? 15 : 20,
                            }}
                            html={Tweet.fullText}
                            onChange={(ev) => {
                              Tweet.fullText = ev.target.value;
                              setState({ activeTweet });
                            }}
                            onBlur={(e) => {
                              Twemoji.parse(e.target);
                            }}
                          />

                          {Tweet.media.length > 0 && (
                            <div
                              className={
                                blurred ? "media remove-resizer" : "media"
                              }
                              style={{
                                marginTop: 8,
                                justifyContent: "center",
                                display: "flex",
                                flexWrap: "wrap",
                              }}
                            >
                              {Tweet.media.map((media, imageIndex) => (
                                <ResizableBox
                                  resizeHandles={["nw"]}
                                  lockAspectRatio={lockAspectRatio}
                                  key={media.url}
                                  //@ts-ignore
                                  style={{
                                    //hidded?.[Tweet.tweetId]?.[index]? "none":
                                    maxHeight: " -webkit-fill-available",
                                    display: media.hidded ? "none" : "block",
                                  }}
                                  width={
                                    windowWidth *
                                    (activeTweet.length > 1 ? 0.4 : 0.75)
                                  }
                                  height={
                                    media.ratio
                                      ? (windowWidth *
                                          (activeTweet.length > 1
                                            ? 0.4
                                            : 0.75)) /
                                        media.ratio
                                      : windowWidth /
                                        (activeTweet.length > 1 ? 3 : 2)
                                  }
                                >
                                  <img
                                    onLoad={(e) => {
                                      const img = e.target as HTMLImageElement;
                                      media.ratio =
                                        img.naturalWidth / img.naturalHeight;
                                      setState({ activeTweet });
                                    }}
                                    onClick={(e) => {
                                      activeTweetIndex.current = index;
                                      activeImageIndex.current = imageIndex;
                                      presentImagePopover({
                                        event: e.nativeEvent,
                                      });
                                    }}
                                    style={{
                                      borderRadius: 16,
                                      width: "100%",
                                      height: "100%",
                                      borderColor: "rgb(56, 68, 77)",
                                    }}
                                    src={media.url}
                                  />
                                </ResizableBox>
                              ))}
                            </div>
                          )}
                        </div>
                      </div>
                    </Draggable>
                  ))}
              </div>
            </div>
          )}
        </div>
        {/* </Twemoji> */}
      </IonContent>

      <IonFooter hidden={blurred} className="ion-no-border">
        <IonToolbar>
          <IonButtons>
            <div
              style={{
                margin: "auto",
                overflowX: "scroll",
                display: "-webkit-box",
              }}
            >
              {/* {[
                { title: "1:1", type: "square" },
                { title: "5:4", type: "portraite" },
                { title: "16:9", type: "story" },
              ].map((button) => (
                <IonButton
                  className={
                    aspectRatio == button.type
                      ? "toolbar-button selected"
                      : "toolbar-button"
                  }
                  key={button.title + "-" + button.type}
                  onClick={() =>
                    button.type &&
                    setAspectRatio(
                      button.type as SetStateAction<
                        "square" | "portraite" | "story"
                      >
                    )
                  }
                >
                  <IonIcon slot="end" icon={logoInstagram} />
                  <IonLabel>{button.title}</IonLabel>
                </IonButton>
              ))} */}

              {/* <IonButton
                className={
                  hasImageBlur ? "toolbar-button selected" : "toolbar-button"
                }
                onClick={() => {
                  setHasImageBlur(!hasImageBlur);
                }}
              >
                <IonIcon slot="end" icon={imageOutline} />
                <IonIcon
                  slot="end"
                  style={{
                    position: "absolute",
                    right: "8px",
                    transform: "scale(0.6)",
                    bottom: "-1px",
                  }}
                  icon={water}
                />
                <IonLabel>Blur</IonLabel>
              </IonButton> */}
              <IonButton
                className={
                  groupedTweets ? "toolbar-button selected" : "toolbar-button"
                }
                onClick={() => {
                  if (!groupedTweets) {
                    const UID = nanoid();
                    activeTweet.push({
                      name: "بینام",
                      userId: "user_" + UID,
                      tweetId: "1234_" + UID,
                      fullText: "متنی بنویسید ...",
                      media: [
                        {
                          url: memes[Math.floor(Math.random() * memes.length)]
                            .url,
                          hidded: false,
                        },
                      ],
                      profileImage:
                        profiles[Math.floor(Math.random() * profiles.length)]
                          .url,
                      userName: "@nameless",
                      shouldHideMedia: true,
                      hasReplay: true,
                      mentions: [
                        {
                          name: "بینام",
                          userId: "mention_user_" + UID,
                          userName: "nameless",
                        },
                      ],
                    });
                  } else activeTweet.pop();

                  setState({ activeTweet });

                  setGroupedTweets(!groupedTweets);
                }}
              >
                <IonIcon slot="end" icon={gitNetworkOutline} />
                <IonLabel>GROUP</IonLabel>
              </IonButton>
              <div
                style={{
                  width: 2,
                  borderRight: "100%",
                  border: "1px solid lightgrey",
                  margin: "auto 2px",
                  height: 20,
                }}
              />
              <IonButton
                className={
                  hasConnector ? "toolbar-button selected" : "toolbar-button"
                }
                onClick={() => {
                  setHasConnector(!hasConnector);
                }}
              >
                <IonIcon slot="end" icon={linkOutline} />
                <IonLabel>CONNECTOR</IonLabel>
              </IonButton>
            </div>
          </IonButtons>
        </IonToolbar>
      </IonFooter>

      <IonModal
        swipeToClose
        isOpen={modalOpened}
        onIonModalWillDismiss={() => nav.replace(nav.location.pathname)}
        presentingElement={router || undefined}
      >
        <div
          style={{
            padding: 8,
            display: "flex",
            flexDirection: "column",
            flex: 1,
            height: "100%",
          }}
        >
          <div
            style={{
              width: 50,
              height: 3,
              backgroundColor: "gray",
              borderRadius: 20,
              margin: "8px auto 16px auto",
            }}
          ></div>
          <IonContent>
            <div
              style={{
                display: "flex",
                justifyContent: "space-around",
                flexWrap: "wrap",
              }}
            >
              {profiles.map((profile, i) => {
                return (
                  <ImageFadeIn
                    onClick={() => {
                      if (activeTweetIndex.current === null)
                        return setModalOpened(false);
                      activeTweet[activeTweetIndex.current].profileImage =
                        profile.url;
                      setState({ activeTweet });
                      setModalOpened(false);
                    }}
                    style={{
                      flexShrink: 0,
                      width: "25%",
                      borderRadius: "100%",
                      margin: 8,
                      border: "3px solid grey",
                    }}
                    key={i}
                    src={profile.url}
                  />
                );
              })}
            </div>
          </IonContent>
        </div>
      </IonModal>
    </>
  );
};

export default Home;
